/* 各方向の初期位置（表示前） */
.fade-up {
  transform: translateY(30px);
}

.fade-down {
  transform: translateY(-30px);
}

.fade-left {
  transform: translateX(30px);
}

.fade-right {
  transform: translateX(-30px);
}

/* アクティブ状態（表示後） */
.fade.is-show {
  opacity: 1;
  transform: translate(0, 0);
}

.js-text-anim {
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
}
.js-text-anim .char {
  display: inline-block;
  transform: translateY(105%);
  opacity: 0;
  transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1), opacity 0.4s ease;
}
.js-text-anim.-visible {
  visibility: visible;
  opacity: 1;
}
.js-text-anim.-visible .char {
  transform: translateY(0);
  opacity: 1;
}

