// ==========================================================================



// ==========================================================================
// About Us Section
// ==========================================================================

.p-home-aboutus {
  @include border-bottom-vw(1, 1, solid, $color-black);
  @include padding-top-vw(100, 50);
  @include padding-bottom-vw(100, 50);
  @include sp-only {
    @include padding-left-vw(0, 20);
    @include padding-right-vw(0, 20);
  }

  .c-description01 {
    @include margin-bottom-vw(50, 30);
    @include sp-only {
      text-align: left;
    }
  }
}

// ==========================================================================
// Ranking Section
// ==========================================================================

.p-home-ranking {
  @include border-bottom-vw(1, 1, solid, $color-black);
  @include padding-top-vw(100, 50);
  @include padding-bottom-vw(100, 50);
  @include sp-only {
    @include padding-left-vw(0, 20);
    @include padding-right-vw(0, 20);
  }
}

// ==========================================================================
// Sliders Layout
// ==========================================================================

.l-products-slider {
  @include sp-only {
    @include margin-left-vw(0, -20);
    @include margin-right-vw(0, -20);
  }
}

.l-instagram-slider {
  @include sp-only {
    @include margin-left-vw(0, -20);
    @include margin-right-vw(0, -20);
  }
}

// ==========================================================================
// Product List Component
// ==========================================================================

.l-products-list {
  display: flex;
  justify-content: space-between;
  @include sp-only {
  }

  &__item {
    @include width-vw(360, 360);
    @include sp-only {
    }
    a {
      display: block;
    }
  }

  &__img {
    position: relative;
    &::after {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 1;
      background-color: $color-white;
      @include border-radius-vw(16, 16);
      @include bottom-vw(-10, -10);
      @include right-vw(-10, -10);
      @include border-vw(1, 1, solid, $color-black);
      @include sp-only {
      }
    }
    img {
      overflow: hidden;
      position: relative;
      z-index: 2;
      @include border-vw(1, 1, solid, $color-black);
      @include border-radius-vw(16, 16);
      @include sp-only {
      }
    }
  }

  &__text01 {
    font-weight: $font-weight-medium;
    @include font-vw(16, 16);
    @include margin-top-vw(25, 25);
    @include sp-only {
    }
  }

  &__text02 {
    @include font-vw(14, 14);
    @include margin-bottom-vw(15, 15);
    @include sp-only {
    }
  }

  &__text03 {
    line-height: 2.2;
    @include font-vw(14, 14);
    @include sp-only {
    }
  }

  &.--slider {
    .l-products-list__item {
      @include width-vw(400, 400);
      @include sp-only {
      }
    }
  }
}

// ==========================================================================
// Swiper Custom
// ==========================================================================

.swiper {
  @include padding-bottom-vw(10, 10);
  @include sp-only {
  }
}

.js-swiper {
  position: relative;
  overflow: hidden;
}

/* 全スライド共通（基準） */
.js-swiper.center-active .swiper-slide {
  transition: transform 0.4s ease, opacity 0.4s ease;
  transform: scale(0.95);
  opacity: 0.7;
}

/* 真ん中（アクティブ）だけ少し大きく */
.js-swiper.center-active .swiper-slide-active {
  transform: scale(1);
  opacity: 1;
}

